<template>
  <div class="row tracking">
    <div class="col-12">
      <button v-if="timeState === 1" type="button" class="btn btn-lg btn-danger" @click="timeTracking" :key="timeState">
        <fa-icon :icon="['fas', 'stop']"/><br>Stop
      </button>
      <button v-else type="button" class="btn btn-lg btn-warning" @click="timeTracking" :key="timeState">
        <fa-icon :icon="['fas', 'play']"/><br>Start
      </button>
    </div>
    <div class="col-12">
      Arbeitszeit: {{ duration }}
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';

dayjs.extend(duration);

export default {
  name: "TimeButtons",
  props: {
    userId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      workingTime: 0,
      breakTime: 0,
      timeState: 0,
      loggedInUser: this.userId,
      duration: '00:00:00',
      timer: null
    }
  },
  watch: {
    userId(newValue) {
      console.log('UserId updated');
      this.$offlineStorage.set('loggedInUser', newValue);
    }
  },
  created() {

  },
  mounted() {
    if (localStorage.workingTime) {
      console.log('Working-Time restored');
      this.workingTime = this.$offlineStorage.get('workingTime');
      this.duration = dayjs.unix(this.workingTime).format('HH:mm:ss');
    }
    if (localStorage.breakTime) {
      console.log('Break-Time restored');
      this.breakTime = this.$offlineStorage.get('breakTime');
    }
    if (localStorage.timeState) {
      console.log('Working-Time-State restored');
      this.timeState = this.$offlineStorage.get('timeState');
    }
    if (localStorage.loggedInUser) {
      console.log('UserId restored');
      this.loggedInUser = this.$offlineStorage.get('loggedInUser');
    }
    if(this.$offlineStorage.get('timer') && this.$offlineStorage.get('timeState') === 1) {
      this.startTimer();
    }
  },
  methods: {
    timeTracking() {
      axios.post(
          'https://testerp.galle-gmbh.de/pwa/toggleTimeTrack',
          {userId: this.loggedInUser, date: dayjs().format('YYYY-MM-DD HH:mm:ss')},
      ).then((response) => {
        this.$offlineStorage.set('workingTime', response.data.workingTime);
        this.$offlineStorage.set('breakTime', response.data.breakTime);

        this.duration = dayjs.duration(this.workingTime, 's').format('HH:mm:ss');
        if (this.timeState === 0) {
          this.$offlineStorage.set('timeState', 1);
          this.timeState = 1;
          this.startTimer();
        } else {
          this.$offlineStorage.set('timeState', 0);
          this.timeState = 0;
          this.stopTimer();
        }
      }).catch((error) => {
        console.log('ERROR-TimeButton: ', error.message);
        console.log(this.isOnline);
        console.log(this.isOffline);
        if (this.isOnline === true) {
          console.log('ERROR-TimeButton: ', error.message);
        } else {
          console.log("in");
          this.duration = dayjs.duration(this.workingTime, 's').format('HH:mm:ss');
          if (this.timeState === 0) {
            this.$offlineStorage.set('timeState', 1);
            this.timeState = 1;
            this.startTimer();
          } else {
            this.$offlineStorage.set('timeState', 0);
            this.timeState = 0;
            this.stopTimer();
          }
        }
      });
    },
    startTimer() {
      if (this.timer === null) {

        this.timer = this.$workerTimers.setInterval(() => {
          this.workingTime += 1;
          this.duration = dayjs.duration(this.workingTime, 's').format('HH:mm:ss');
        }, 1000);
        this.$offlineStorage.set('timer', this.timer);
        /*
        this.timer = setInterval(() => {
          this.workingTime += 1;

          this.duration = this.$workerTimers.set
          this.duration = dayjs.duration(this.workingTime, 's').format('HH:mm:ss');
        },1000);

         */
      }
      console.log(this.timer);
    },
    stopTimer() {
      if (this.$offlineStorage.get('timer') === this.timer) {
        this.$workerTimers.clearInterval(this.timer);
      }


      //clearInterval(this.timer);
      this.timer = null;
    }
  }

}
</script>

<style scoped>
.tracking button {
  height: 10rem;
  width: 10rem;
}


</style>